.app {
  flex-direction: column;
  width: 100%;
  height: 100vh;
  display: flex;
}

.header {
  color: #fff;
  background: linear-gradient(135deg, #006bff, #7b60e1);
  justify-content: space-between;
  align-items: center;
  height: 4rem;
  padding: 0 1.5rem;
  display: flex;
  box-shadow: 0 4px 20px -2px #006bff1a;
}

.header .logo {
  max-width: 40px;
}

.header .header-left {
  align-items: center;
  gap: 1rem;
  font-size: .85rem;
  display: flex;
}

.header .header-right {
  align-items: center;
  gap: 2rem;
  font-size: .85rem;
  display: flex;
}

.header .header-right .menu-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: 1px solid #fff;
  border-radius: .375rem;
  align-items: center;
  padding: .5rem;
  font-size: .875rem;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header .header-right .menu-btn .menu-icon {
  width: 1rem;
  height: 1rem;
}

.header .header-right .menu-btn:hover {
  color: #006bff;
  background-color: #fff;
}

.header .header-right .menu-btn.active {
  opacity: .8;
  transform: scale(.95);
}

.main-content {
  flex: 1;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  min-height: 0;
  padding: 1rem;
  display: grid;
  overflow: hidden;
}

.left-column, .right-column {
  flex-direction: column;
  gap: 1rem;
  min-height: 0;
  display: flex;
}

.collapsible.expanded {
  flex: 1;
  min-height: 0;
}

.collapsible.expanded .collapse-icon {
  transform: rotate(-180deg);
}

.collapsible.expanded .card-content {
  padding-top: 0;
  display: block;
}

.card {
  background: #fff;
  border-radius: .75rem;
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 20px -2px #006bff1a;
}

.card .card-header {
  border-bottom: 1px solid #e5e7eb;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.card .card-header.clickable {
  cursor: pointer;
  border-bottom: none;
}

.card .card-header.clickable:hover {
  background-color: #f9fafb80;
}

.card .card-header .section-title {
  color: #1c1c1c;
  font-size: .875rem;
  font-weight: 600;
}

.card .card-header .collapse-icon {
  width: 1.25rem;
  height: 1.25rem;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
}

.card .card-content {
  flex: 1;
  padding: 1rem;
  display: none;
  overflow: hidden;
}

.chat-section {
  flex: 1;
  min-height: 0;
  display: flex;
}

.chat-section .chat-messages {
  flex-direction: column;
  flex: 1;
  gap: .85rem;
  min-height: 0;
  display: flex;
  overflow-y: auto;
}

.chat-section .chat-messages .message-block {
  align-items: flex-start;
  gap: .5rem;
  display: flex;
}

.chat-section .chat-messages .message-block .message-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.chat-section .chat-messages .message-block .message-info .message-avatar {
  background: linear-gradient(123deg, #006bff 19%, #7b60e1 88.6903%);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  padding: 6px;
  display: flex;
}

.chat-section .chat-messages .message-block .message-info .message-avatar img {
  width: 100%;
  height: auto;
}

.chat-section .chat-messages .message-block .message-info .audio-icon {
  justify-content: center;
  align-items: flex-end;
  gap: 2px;
  width: 100%;
  height: 12px;
  margin-top: 8px;
  display: none;
}

.chat-section .chat-messages .message-block .message-info .audio-icon.show {
  display: flex;
}

.chat-section .chat-messages .message-block .message-info .audio-icon .bar {
  width: 1.5px;
  height: 100%;
  animation: 1s ease-in-out infinite bounce;
  animation-delay: calc(var(--i) * .1s);
  background: #006bff;
  border-radius: 2px;
}

.chat-section .chat-messages .message-block .message-info .audio-icon.pause .bar {
  animation: .5s ease-in-out pause;
}

@keyframes bounce {
  0%, 100% {
    transform: scaleY(.3);
  }

  50% {
    transform: scaleY(1);
  }
}

@keyframes pause {
  0% {
    transform: scaleY(.3);
  }

  100% {
    transform: scaleY(0);
  }
}

.chat-section .chat-messages .message-block.message-Turi .message-content {
  background-color: #f1f2f9;
}

.chat-section .chat-messages .message-block.message-UnvoicedNote .message-content {
  width: 100%;
  max-width: 100%;
  padding: .5rem 0;
}

.chat-section .chat-messages .message-block.message-UnvoicedNote .message-content p {
  color: #464a4f;
  font-size: .85rem;
}

.chat-section .chat-messages .message-block.message-student .message-content {
  color: #fff;
  background-color: #006bff;
  margin-left: auto;
}

.chat-section .chat-messages .message-block.message-student .message-content span.choice {
  margin-right: .5rem;
}

.chat-section .chat-messages .message-content {
  border-radius: .5rem;
  max-width: 80%;
  padding: .5rem .75rem;
}

.chat-section .chat-messages .message-content p {
  text-align: left;
  margin: 0;
  line-height: 1.4;
}

.chat-section .chat-messages .message-content p span:not(.choice) {
  align-items: baseline;
  display: inline-flex;
}

.control-section {
  height: 12rem;
  display: flex;
}

.control-section .control-content {
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
  height: 100%;
  display: flex;
}

.control-section .control-content .control-area {
  flex: 1;
}

.control-section .control-content .control-area.is-input {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: .5rem;
  padding: 1rem;
}

.control-section .control-content .control-area p {
  color: #464a4f;
  margin: 0;
  font-size: .875rem;
}

.control-section .control-content .action-buttons {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.control-section .control-content .action-buttons .action-btn {
  color: #1c1c1c;
  cursor: pointer;
  background: none;
  border: 1px solid #e5e7eb;
  border-radius: .375rem;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 40px;
  font-size: .875rem;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.control-section .control-content .action-buttons .action-btn svg {
  width: 24px;
  height: 24px;
}

.control-section .control-content .action-buttons .action-btn:hover {
  background-color: #f9fafb;
  border-color: #006bff;
}

.control-section .control-content .action-buttons .action-btn.active {
  opacity: .8;
  transform: scale(.95);
}

.notes-section .card {
  position: relative;
}

.notes-section .card .card-content img {
  object-fit: cover;
  width: 100%;
  max-height: 100%;
}

#startPopup {
  z-index: 9999;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#startPopup > div {
  opacity: 0;
  text-align: center;
  background: #fff;
  border-radius: 10px;
  max-width: 400px;
  padding: 30px;
  animation: .2s ease-out .2s forwards popupZoom;
  box-shadow: 0 .517525px .310515px -.75px #888ae378, 0 1.56943px .941657px -1.5px #888ae375, 0 4.14872px 2.48924px -2.25px #888ae36e, 0 13px 7.8px -3px #888ae352, inset 0 0 0 -1.875px #ffffff3b, inset 0 0 0 -3.75px #ffffff17, inset 0 0 2px #1e21734d;
}

#startPopup > div button {
  color: #fff;
  background: linear-gradient(123deg, #006bff 19%, #7b60e1 88.6903%);
  border-radius: 24px;
  flex-wrap: nowrap;
  height: 48px;
  margin-top: 12px;
  padding: 14px 20px;
  line-height: 0;
  box-shadow: 0 .517525px .310515px -.75px #888ae378, 0 1.56943px .941657px -1.5px #888ae375, 0 4.14872px 2.48924px -2.25px #888ae36e, 0 13px 7.8px -3px #888ae352, inset 0 0 0 -1.875px #ffffff3b, inset 0 0 0 -3.75px #ffffff17, inset 0 0 2px #1e21734d;
}

#choicePopup {
  opacity: 0;
  text-align: center;
  background: #fff;
  padding: 1.5rem 1rem;
  display: none;
  box-shadow: 0 .517525px .310515px -.75px #888ae378, 0 1.56943px .941657px -1.5px #888ae375, 0 4.14872px 2.48924px -2.25px #888ae36e, 0 13px 7.8px -3px #888ae352, inset 0 0 0 -1.875px #ffffff3b, inset 0 0 0 -3.75px #ffffff17, inset 0 0 2px #1e21734d;
}

#choicePopup .choice-btn {
  text-align: left;
  cursor: pointer;
  background: #fff;
  border: 2px solid #006bff;
  border-radius: .75rem;
  align-items: center;
  padding: 1rem;
  font-size: 1rem;
  transition: all .2s;
  display: flex;
  box-shadow: 0 4px 10px -2px #006bff1a;
}

#choicePopup .choice-btn:hover {
  color: #fff;
  background: #006bff;
  box-shadow: 0 6px 12px -2px #006bff1a;
}

#choicePopup .choice-btn span.choice {
  margin-right: .85rem;
  font-size: 1.25rem;
  font-weight: 700;
}

body.show-choice #choicePopup {
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  min-width: 100vw;
  animation: .2s ease-out .2s forwards popupZoom;
  display: grid;
}

.app-loader {
  z-index: 9999;
  place-items: start center;
  padding-top: 60px;
  transition: opacity .2s;
  display: grid;
  position: absolute;
  inset: 0;
}

.app-loader.hidden {
  opacity: 0;
  pointer-events: none;
  display: none;
}

.app-loader__spinner {
  border: 4px solid #006bff;
  border-top-color: #fff0;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin-bottom: 10px;
  animation: .8s linear infinite app-spin;
}

@keyframes app-spin {
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: reduce) {
  .app-loader__spinner {
    animation: none;
  }
}

@media (width <= 1024px) {
  .header .header-left, .header .header-right {
    gap: 1rem;
  }

  .header .header-right .main-title {
    font-size: 1rem;
  }

  body.show-choice #choicePopup {
    position: fixed;
    bottom: 0;
  }
}

@media (width <= 798px) {
  .student-topic-info, topic-info, .title-section, .study-info {
    display: none;
  }

  .main-content {
    flex-flow: column;
    display: flex;
  }

  .left-column {
    height: 100%;
  }

  body.show-choice .control-section {
    display: none;
  }

  body.show-choice #choicePopup {
    position: relative;
  }

  #startPopup {
    padding: 0 10vw;
  }
}
